<template>
  <div class="getPointsBox">
    <div class="header">
      <div class="orderDetailsHeader">
        <div class="headerContent">
          <van-icon name="arrow-left" @click="$router.go(-1)" />
          <span>{{ $t("index.text1") }}</span>
        </div>
      </div>
      <div class="slot"></div>
    </div>

    <div class="orderDetailsMain">
      <div class="recommendedInfo " style="background: #fcefd3">
        <div class="recommendedInfoTitle jcsb">
          <span style="color:#333"> （USDT）{{ $t("index.text12") }} </span>
          <span style="color:#333">{{ usdt }}</span>
        </div>

        <div class="recommendedInfoTitle jcsb mt2r">
          <span style="color:#333"> （HF）{{ $t("index.text12") }} </span>
          <span style="color:#333"> {{ hf }}</span>
        </div>
      </div>
      <!-- <div class="getPoints jcsb">
        <div class="getPointsItem" @click="$router.push('/getPoints')">
          <div class="text">（USDT）{{ $t("index.text12") }}</div>
          <div class="text1">{{ usdt }}</div>
        </div>
        <div class="getPointsItem">
          <div class="text">（HF）{{ $t("index.text12") }}</div>
          <div class="text1">{{ hf }}</div>
        </div>
      </div> -->

      <div class="buyBox">
        <input
          type="text"
          :placeholder="$t('index.text13')"
          v-model.trim="quantity"
        />

        <div class="price">{{ $t("index.text170") }}{{ hlPrice }}</div>
        <div class="price">
          {{ $t("index.text181") }}{{ fromWei(membersInfo.price || 0) }}
        </div>
        <!-- <div class="price">{{ $t("index.text182") }}{{ increase }}%</div> -->
        <!-- 
        <div class="pic16 jcc">
          <img src="../../assets/img/pic16.png" alt="" />
        </div> -->

        <!-- <input type="text" disabled v-model="purchaseQuantity" /> -->

        <div class="buyBtn jcc" @click="buyHl">{{ $t("index.text10") }}</div>
      </div>

      <div class="tips">
        {{ $t("index.text14")
        }}<span style="color: #d12c21"> {{ $t("index.text15") }}</span>
        <span style="color: #f5c96d"> {{ $t("index.text16") }}</span>
        {{ $t("index.text17") }}
      </div>

      <div style="margin-top: 2rem" class="jcc">
        <span
          @click="$router.push('/purchaseRecords')"
          style="text-decoration: underline; font-weight: bold"
        >
          {{ $t("index.text18") }}</span
        >
      </div>

      <!-- <div style="margin-top: 2rem" class="jcc">
        <span
          @click="authorization"
          style="text-decoration: underline; font-weight: bold"
        >
          授权</span
        >
      </div> -->

      <div class="recommendedInfo mt2r">
        <div class="recommendedInfoTitle jcsb">
          <span> {{ $t("index.text179") }} </span>
          <span>{{ membersInfo.count || 0 }}</span>
        </div>

        <div class="recommendedInfoTitle jcsb mt2r">
          <span> {{ $t("index.text180") }} </span>
          <span> {{ coreBalance }}</span>
        </div>
      </div>
    </div>
    <van-popup v-model="rewardPointsPopup" class="rewardPointsPopup">
      <div class="content">
        <img
          src="../../assets/img/close.png"
          class="close"
          alt=""
          @click="rewardPointsPopup = false"
        />
        <div class="title">{{ $t("index.text155") }}</div>

        <textarea
          class="textarea"
          type="textarea"
          :placeholder="$t('index.text156')"
          v-model.trim="superiorsAddr"
          rows="7"
        ></textarea>
        <div class="confirm mt2r jcc" @click="bind">
          {{ $t("index.text10") }}
        </div>

        <div class="tips">{{ $t("index.text157") }}</div>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  components: {},

  data() {
    return {
      quantity: "",
      price: "",
      hl: 0,
      hf: 0,
      usdt: 0,
      hlPrice: 0,
      purchaseQuantity: "HL",
      rewardPointsPopup: false,
      superiorsAddr: "",
      recommerMapping: "",
      membersInfo: {},
      coreBalance: 0
    };
  },
  async activated() {
    // this.getMembers();
    this.getBalance();
    this.refereeToRecommender();
    // setInterval(() => {
    //   this.getPrice();
    // }, 2000);
  },
  computed: {
    increase() {
      let a =
        Number(this.hlPrice) - Number(this.fromWei(this.membersInfo.price));

      return (a / Number(this.fromWei(this.membersInfo.price))) * 100;
    }
  },
  methods: {
    async getMembers() {
      // this.$api.get("user/count", {}).then(res => {
      //   this.membersInfo = res.data;
      // });
      let hl = await this.core
        .hlContract()
        .balanceOf(this.core.coreAddress)
        .call();
      this.coreBalance = this.fromWei(hl);
    },
    async authorization() {
      let gasPrice = await this.getGasPrice();
      let gas = await this.core
        .usdtContract()
        .approve(this.core.coreAddress, this.toWei(10000))
        .estimateGas();
      await this.core
        .usdtContract()
        .approve(this.core.coreAddress, this.toWei(10000))
        .send({
          from: this.$store.state.userAddress,
          gas: gas + 5000,
          gasPrice: gasPrice
        });

      let gas1 = await this.core
        .hfContract()
        .approve(this.core.coreAddress, this.toWei(10000))
        .estimateGas();
      await this.core
        .hfContract()
        .approve(this.core.coreAddress, this.toWei(10000))
        .send({
          from: this.$store.state.userAddress,
          gas: gas1 + 5000,
          gasPrice: gasPrice
        });

      let gas2 = await this.core
        .hlContract()
        .approve(this.core.coreAddress, this.toWei(10000))
        .estimateGas();
      await this.core
        .hlContract()
        .approve(this.core.coreAddress, this.toWei(10000))
        .send({
          from: this.$store.state.userAddress,
          gas: gas2 + 5000,
          gasPrice: gasPrice
        });
      this.$toast("授权成功");
    },
    // 查询上级
    async refereeToRecommender() {
      this.recommerMapping = await this.core
        .coreContract()
        ._recommerMapping(this.userAddress)
        .call();
      if (this.recommerMapping == this.zeroAddress) {
        this.rewardPointsPopup = true;
      }
    },

    async getPrice() {
      let res = await this.core
        .coreContract()
        .getPrice()
        .call();
      this.hlPrice = Number(this.fromWei(res));
    },
    async getBalance() {
      this.getPrice();
      let hl = await this.core
        .hlContract()
        .balanceOf(this.userAddress)
        .call();
      let hf = await this.core
        .hfContract()
        .balanceOf(this.userAddress)
        .call();
      let usdt = await this.core
        .usdtContract()
        .balanceOf(this.userAddress)
        .call();

      this.hl = this.fromWei(hl);
      this.hf = this.fromWei(hf);
      this.usdt = this.fromWei(usdt);
    },
    async buyHl() {
      if (this.recommerMapping == this.zeroAddress) {
        this.rewardPointsPopup = true;
        return;
      }
      if (!this.quantity) {
        this.$toast(this.$t("index.text19"));
        return;
      }

      if (Number(this.usdt) < Number(this.quantity)) {
        this.$toast(this.$t("index.text164"));
        return;
      }

      if (Number(this.quantity) > 100) {
        this.$toast(this.$t("index.text169"));
        return;
      }
      let daily = await this.core
        .coreContract()
        .dailyPurchases(this.userAddress)
        .call();

      // if (Number(this.fromWei(daily)) >= 1000) {
      //   this.$toast(this.$t("index.text163"));
      //   return;
      // }
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        message: this.$t("index.text21"),
        className: "loading"
      });

      let gasPrice = await this.getGasPrice();
      let allowance = await this.core
        .usdtContract()
        .allowance(this.userAddress, this.core.coreAddress)
        .call();

      let a = Number(this.fromWei(allowance));
      let b = Number(this.quantity);
      try {
        if (a < b) await this.publicApprove(this.core.coreAddress, this.usdt);
        // let gas2 = await this.core
        //   .hlContract()
        //   .approve(this.core.coreAddress, this.toWei(this.hl))
        //   .estimateGas();
        // await this.core
        //   .hlContract()
        //   .approve(this.core.coreAddress, this.toWei(this.hl))
        //   .send({
        //     from: this.userAddress,
        //     gas: gas2 + 5000,
        //     gasPrice: gasPrice
        //   });
        let gas = await this.core
          .coreContract()
          .buyHL(this.userAddress, this.toWei(this.quantity))
          .estimateGas();
        let res = await this.core
          .coreContract()
          .buyHL(this.userAddress, this.toWei(this.quantity))
          .send({
            from: this.userAddress,
            gas: gas + 10000,
            gasPrice: gasPrice
          });
        this.$toast.clear();
        this.getBalance();
        this.$toast(this.$t("index.text22"));
      } catch (error) {
        this.setErrDialog(this.$t("index.text178"));
        this.$toast.clear();
        this.getBalance();
      }
    },
    async bind() {
      if (!this.superiorsAddr) {
        this.$toast(this.$t("index.text156"));
        return;
      }

      if (!this.isAddress(this.superiorsAddr)) {
        this.$toast(this.$t("index.text71"));
        return;
      }

      if (this.superiorsAddr.toLowerCase() == this.userAddress.toLowerCase()) {
        this.$toast(this.$t("index.text159"));
        return;
      }
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        message: this.$t("index.text21"),
        className: "loading"
      });
      try {
        let gasPrice = await this.getGasPrice();

        let gas = await this.core
          .coreContract()
          .addRelationEx(this.userAddress, this.superiorsAddr)
          .estimateGas();

        this.referrals = await this.core
          .coreContract()
          .addRelationEx(this.userAddress, this.superiorsAddr)
          .send({
            from: this.userAddress,
            gas: gas + 10000,
            gasPrice: gasPrice
          });
        this.$toast(this.$t("index.text158"));
        this.refereeToRecommender();
        this.rewardPointsPopup = false;
      } catch (error) {
        console.log(error);
        this.$toast.clear();
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.getPointsBox {
  background: #f7f7f7;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.orderDetailsHeader {
  padding: 0 2rem;
  background: #d12c21;
  position: fixed;
  top: 0;
  width: 100%;
  height: 5rem;
  z-index: 99;

  .headerContent {
    display: flex;
    align-items: center;
    position: relative;
    height: 5rem;
  }
  .van-icon {
    font-size: 1.8rem;
    color: #fff;
  }

  span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    font-size: 1.6rem;
  }
}
.orderDetailsMainHeader {
  background: #bd303e;
  height: 37.5rem;
}
.orderDetailsMain {
  flex: 1;
  overflow-x: hidden;
  padding: 1.6rem;

  .getPoints {
    .getPointsItem {
      width: 16.5rem;
      height: 7.4rem;
      background: #fcefd3;
      border-radius: 0.6rem;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
      .text {
        font-size: 1.4rem;
      }
      .text1 {
        font-weight: bold;
        font-size: 2rem;
      }
    }
  }

  .buyBox {
    margin-top: 2.5rem;
    background: #fcefd3;
    border-radius: 0.6rem;
    padding: 2rem 2.4rem;
    .price {
      margin-top: 2rem;
    }
    input {
      width: 100%;
      border: 0;
      height: 4rem;
      border-radius: 0.6rem;
      padding-left: 1rem;
      box-sizing: border-box;
      font-size: 1.4rem;
      color: #000;
    }
    input::placeholder {
      color: #000;
    }
    input:disabled {
      background: #fff;
    }
    .buyBtn {
      margin-top: 3rem;
      color: #fff;
      height: 4rem;
      background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
      box-shadow: 2px 5px 20px 1px rgba(255, 16, 0, 0.28);
      border-radius: 0.4rem;
    }

    .pic16 {
      margin: 2rem 0;
      img {
        width: 2rem;
        height: 2rem;
      }
    }
  }

  .tips {
    margin-top: 1.6rem;
    line-height: 3rem;
  }
}
.recommendedInfo {
  padding: 1.2rem 1.4rem;
  border-radius: 0.4rem;
  background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);

  .recommendedInfoTitle {
    span {
      color: #fff;
    }
  }

  .tips {
    margin-top: 1.2rem;
    line-height: 2.4rem;
    color: #fff;
    font-size: 1.2rem;
  }
}
.rewardPointsPopup {
  border-radius: 0.5rem;
  padding: 2rem;
  width: 90vw;
  background: #fcefd3;
  .content {
    position: relative;
  }
  .close {
    position: absolute;
    top: 0;
    right: 0;
    width: 2rem;
    height: 2rem;
  }
  .title {
    text-align: center;
    font-size: 1.6rem;
    font-weight: bold;
  }

  .tips {
    margin-top: 1.5rem;
    line-height: 2.5rem;
  }

  .textarea {
    margin-top: 1.6rem;
    width: 94%;
    border: 0;
    padding: 1.4rem 0 0 1.6rem;
    border-radius: 0.5rem;
  }

  .textarea::placeholder {
    color: #000;
  }
  .confirm {
    height: 5rem;
    background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
    box-shadow: 2px 5px 20px 1px rgba(255, 16, 0, 0.28);
    border-radius: 0.8rem;
    font-size: 1.6rem;
    color: #fff;
    font-weight: bold;
  }
}
</style>



